

.menu {
	position: fixed;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100px;
	z-index: 5;
	.menu-inner{
		display: block;
		height: $menu-height;
		margin: 25px auto 0;
		max-width: 1200px;
		margin: 0 auto;
		width: 100%;
		position: relative;
		font-family: Questrial;
		//border
		.menu-wrapper{
				width: 100%;
				height: $menu-height;
				.hamburger-icon{
					display: none;
					position: absolute;
					top: 80px;
					right: 10%;
					color: $white;
					transition: all 1s ease;
					&:hover{
						color: $light-orange;
						cursor: pointer;
					}
					&.expanded{
						color: $light-orange;
					}
				}
				//border-bottom: 1px solid $white;
				@include breakpoint($lg){
					.hamburger-icon{
						display: block;
					}
					.menu-items{
						display: none;
					}
				}
		}
	}
	.menu-title{
		float: left;
		font-size: 32px;
		color: $white;
		font-weight: 400;
		line-height: 100px;
	}
	.logo-wrapper{
		float:left;
		width: 25%;
		min-width: 300px;
		margin-top: 50px;
		@include breakpoint($lg){
			position: absolute;
			top: 25px;
			left: 10%;
			float: none;
			margin-top: 0;
		}
		.menu-logo{
			width: 300px;
			height: auto;
			@include breakpoint($lg){
				width: 150px;
			}
		}
	}

	.menu-items{
		width: 65%;
		float: right;
		display: block;
		margin: 0;
		@include breakpoint($lg){
			display: none;
		}
		&.expanded{
			display: block !important;
			position: absolute;
			top: -$menu-mobile;
			width: 100%;
			padding: 0 0;
			height: $menu-mobile;
			.menu-item{
				color: $grey2;
				float: left;
				width: 80%;
				margin: auto 10%;
				height: $menu-mobile/5-1px;
				line-height: $menu-mobile/5-1px;
				text-align: center;
				z-index: -1;
				border-bottom: 1px solid $grey2;
				&:last-child{
					border: 0;
				}
			}
		}
		.menu-item{
			color: $white;
			display: block;
			float: right;
			margin-top: $menu-height + 35px;
			padding: 0 25px;
			font-size: 16px;
			font-weight: 600;
			text-transform: uppercase;
			transition: color 1s ease;
			@include breakpoint($lg){
				margin-top: 0;
			}
			&:hover{
				color: $light-orange;
				cursor: pointer;
			}
		}
	}
}
